@import "color";

.confirmOrder {
  background-color: $pink;
  width: 100%;
  height: 100vh;
  > main {
    background-color: white;
    max-width: 900px;
    margin: auto;
    padding: 2rem;

    > h1 {
      text-transform: uppercase;
      text-align: center;
      margin: 3rem;
      font: 100 3rem "Roboto";
    }
    > form {
      display: flex;
      flex-direction: column;
      align-items: center;

      > div {
        background-color: rgb(233, 233, 233);
        margin: 1rem;
        padding: 1rem;
        border-radius: 5px;
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        > label {
          white-space: nowrap;
        }
      }
      > button,
      a {
        background-color: $red;
        color: white;
        outline: none;
        padding: 0.5rem 1rem;
        border-radius: 5px;
        margin: 1rem;
        text-align: center;
        cursor: pointer;
        font-size: 1.1rem;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .confirmOrder {
    > main {
      display: flex;
      flex-direction: column;
      justify-content: center;
    >h1{
        font-size: 1.5rem;
    }
      > form {
        > div {
          width: 100%;
          
        }
        > button {
          width: 100%;
        }
      }
    }
  }
}
